/* reset.css */
html, body, div, span, applet, object, iframe,h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,small, strike, strong, sub, sup, tt, var,b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; font-size: 100%; font: inherit; vertical-align: baseline; }
	
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { display: block; }
html, body { height: 100%; -webkit-text-size-adjust: none; }
body, h1, h2, h3, h4, h5, h6 { line-height: 1.5; color: inherit; }
ol, ul, li { list-style: none; }
a { color: inherit; text-decoration: none; }
blockquote, q { quotes: none; }
blockquote:before, blockquote:after, q:before, q:after { content: ''; content: none; }
table { border-collapse: collapse; border-spacing: 0; }
table caption{ width: 0; height: 0; line-height: 0; text-indent: -99999px; font-size: 0; visibility: hidden; overflow: hidden; }
input, button, form, select { padding: 0; margin: 0; font-family: inherit; font-size: inherit; }
input[type=text], input[type=password], input[type=submit], input[type=search], input[type=email], input[type=tel], textarea, select { -webkit-appearance: none; border-radius: 0; }
button { cursor: pointer; border: 0; background-color: transparent; }

.hide, legend { position: absolute; top: 0; left: -9999px; }